import React, { useState } from 'react'
import PropTypes from 'prop-types'
import { connect } from "react-redux";
import { Form,Card,Switch} from 'antd'
import { CheckOutlined,CloseOutlined } from '@ant-design/icons'
const FacilityField = (props)=> {

    const [ form ] = Form.useForm()
    const [ fields ] = useState({
        gazebo:{
            name:['facility','gazebo'],
            label:"Gazebo",
            rules:[]
        },
        ac:{
            name:['facility','ac'],
            label:"AC",
            rules:[]
        },
        tv:{
            name:['facility','tv'],
            label:"TV",
            rules:[]
        },
        hall:{
            name:['facility','hall'],
            label:"Hall",
            rules:[]
        },
        wifi:{
            name:['facility','wifi'],
            label:"Wifi",
            rules:[]
        },
        swimming_pool:{
            name:['facility','swimming_pool'],
            label:"Swimming Pool",
            rules:[]
        },
        parking:{
            name:['facility','parking'],
            label:"Parking",
            rules:[]
        },
        meeting_room:{
            name:['facility','meeting_room'],
            label:"Meeting Room",
            rules:[]
        },
    })
    return (
        <Card bordered={true} title={'Facility'}>
            <div className="flex flex-wrap">
                {
                    Object.keys(fields).map((key)=> {
                        return (
                            <div className={'w-[50%]'}>
                                <Form.Item {...fields[key]}>
                                    <Switch
                                        checkedChildren={<CheckOutlined />}
                                        unCheckedChildren={<CloseOutlined />}
                                        defaultChecked={props?.data?.facility[key] ?? false}
                                    />
                                </Form.Item>
                            </div>
                        )
                    })
                }
            </div>
        </Card>
    )
}

export default connect(({Villa})=> {
    let { detail } = Villa
    let { data, loading } = detail
    return {data,loading}
},{})(React.memo(FacilityField))