import React, {useState, useEffect} from 'react'
import PropTypes from 'prop-types'
import {connect} from 'react-redux'
import {Card, Upload, message, Row, Col, Button, Image, Tooltip, notification} from 'antd'
import Utils from "../../../../utils";
import {DeleteOutlined, InboxOutlined, PlusCircleOutlined, UploadOutlined, FullscreenOutlined} from "@ant-design/icons";
import {_deleteImagesRoom} from "../../../../redux/actions";
import ApiService from "../../../../services/Api.service";

const {Dragger} = Upload;
const PhotoFields = (props) => {
    let {_deleteImagesRoom,data} = props

    const beforeUpload = file => {
        const isJpgOrPng = file.type === 'image/jpeg' || file.type === 'image/png' || file.type === 'image/jpg';
        if (!isJpgOrPng) {
            message.error('You can only upload JPG/PNG file!');
        }
        const isLt2M = file.size / 1024 / 1024 < 2;
        if (!isLt2M) {
            message.error('Image must smaller than 2MB!');
        }
        return isJpgOrPng && isLt2M;
    }


    const onDelete = (imageId) => {}
    const [visible, setVisible] = useState(false);
    const [newImages, setNewImages] = useState([]);
    const onUploadChange = async  (info) => {
        const {onSuccess, onError, file, onProgress} = info;

        const formdata = new FormData()
        formdata.append(`files`, file)

        await new ApiService({
            url: `/api/v1/villa/self/profile/photo`,
            body: formdata,
            config: {
                headers: {
                    "Authorization": `Bearer ${Utils.getToken('get')}`,
                    "Content-Type": "application/formdata"
                },
                onUploadProgress: event => {
                    let progress = (event.loaded / event.total) * 100
                    if (progress >= 100){

                    }
                    onProgress({percent: progress}, file);
                }
            }
        })
            .put()
            .then(async ({data}) => {
                let newArr = []
                await Utils.getBase64(file, imgUrl => {
                    newArr.push(imgUrl)
                });
                for(let i = 0 ; i < newImages.length;i++){
                    newArr.push(newImages[i])
                }
                setNewImages(newArr)

                onSuccess(file);



                notification.success({
                    message: 'Success save to database',
                    description: 'the data has been successfully updated',
                    duration: 2
                });
                // setLoading(false)
            })
            .catch((err) => {
                const error = new Error('Some error');
                onError({event: error});
                message.error({
                    content: err?.message ??  "Successfully!",
                    key: "updatable",
                    duration: 2
                })
            })
    }
    const onVisible = () => {
        setVisible(true)
    }

    return data !== null &&(
        <Card title={"Gallery Photos"}
              loading={props?.data === null}
              extra={<>
                  <div className={'flex items-center gap-2'}>
                      <Tooltip title={'Open in Modal'}>
                          <Button
                              type={'primary'}
                              ghost
                              onClick={onVisible}
                              icon={<FullscreenOutlined/>}/>
                      </Tooltip>

                  </div>
              </>}

        >
            <div className="clearfix">
                <div className="pb-10">
                    <Upload
                        accept="image/*"
                        multiple={false}
                        listType="picture"
                        progress={{
                            strokeColor: {
                                '0%': '#108ee9',
                                '100%': '#87d068',
                            },
                            strokeWidth: 3,
                            format: percent => percent && `${parseFloat(percent.toFixed(2))}%`,
                        }}
                        maxCount={10}
                        onRemove={false}
                        customRequest={onUploadChange}
                        className={'image-upload-grid'}
                        beforeUpload={beforeUpload}
                    >
                        <Button>
                            <UploadOutlined/> Add New Photos
                        </Button>
                    </Upload>
                </div>

                {
                    !props?.loading &&
                    typeof (props?.data) !== "undefined" &&
                    (
                        <div className={'w-full flex justify-start flex-wrap gap-4 '}>
                            {
                                newImages.map((item, index) => (
                                    <div className={'w-[220px] md:w-[320px]'} key={`image-item-${index}`}>
                                        <div className="relative rounded-[20px] h-full w-full overflow-hidden">
                                            <div className={'absolute top-2 right-3 z-10'}>
                                                <Button
                                                    type={'primary'}
                                                    loading={false}
                                                    size={'small'}
                                                >NEW</Button>
                                            </div>
                                            <img
                                                src={item}
                                                alt={item}
                                                className={'w-full h-[180px] md:h-[250px] object-cover'}
                                            />
                                        </div>
                                    </div>
                                ))
                            }
                            {
                                !props?.loading &&
                                Array.isArray(props?.data?.photos) &&
                                props?.data?.photos.length > 0 &&
                                props?.data?.photos.map((item, index) => (
                                    <div className={'w-[220px] md:w-[320px]'} key={`image-item-${index}`}>
                                        <div className="relative rounded-[20px] h-full w-full overflow-hidden">
                                            <div className={'absolute top-2 right-3 z-10'}>
                                                <Button
                                                    onClick={() => onDelete(item?._id)}
                                                    loading={item?.loading ?? false}
                                                    size={'small'}
                                                    icon={<DeleteOutlined/>}/>
                                            </div>
                                            <img
                                                src={[item?.prefix_url, item?.filename].join("/")}
                                                alt={[item?.prefix_url, item?.filename].join("/")}
                                                className={'w-full h-[180px] md:h-[250px] object-cover'}
                                            />
                                        </div>
                                    </div>
                                ))
                            }


                        </div>
                    )
                }
                <div style={{display: 'none'}}>
                    {
                        typeof (props?.data) !== "undefined" &&
                        typeof (props?.data?.photos) !== "undefined" && Array.isArray(props?.data?.photos) && props?.data?.photos.length > 0 && (
                            <Image.PreviewGroup preview={{visible, onVisibleChange: vis => setVisible(vis)}}>
                                {
                                    props?.data?.photos.length > 0 &&
                                    props?.data?.photos.map((item) => (
                                        <Image src={[item?.prefix_url, item?.filename].join("/")}/>
                                    ))
                                }
                            </Image.PreviewGroup>
                        )
                    }
                </div>


            </div>
        </Card>
    )
}
PhotoFields.propTypes = {
    _deleteImagesRoom: PropTypes.func.isRequired
}
PhotoFields.defaultProps = {}

export default connect(({Villa}) => {
    let {detail} = Villa
    let {data, loading} = detail
    return {data, loading}
}, {_deleteImagesRoom})(PhotoFields)
