import React, {useState, useEffect} from 'react'
import PropTypes from 'prop-types'
import {connect} from 'react-redux'
import {Badge, Card, Form, Input, InputNumber, Select} from "antd";
import {GetDistricts, GetProvinces, GetRegencies, GetVillages} from "../../../../redux/actions/Locations";

const LocationField = (props) => {
    let {
        province,
        regencies,districts,
        vilages,
        GetProvinces,
        GetRegencies,
        GetDistricts,
        GetVillages
    } = props

    useEffect(() => {
        GetProvinces({
            params: {
                ...province?.params,
                limit: 50
            }
        })
    }, [GetProvinces]);

    const selectedProvince = (id,data) => {
        GetRegencies({
            params: {
                limit: 20,
                page: 1,
                province_id: data?.id
            }
        })
    }

    const selectRegencies = (id, data)=> {
        GetDistricts({
            params: {
                limit: 20,
                page: 1,
                regency_id: data?.id
            }
        })
    }

    const selectDistrict = (id,data) => {
        GetVillages({
            params: {
                limit:20,
                page:1,
                district_id: data?.id
            }
        })
    }

    useEffect(()=> {
        if(!props?.detail?.loading  && props?.detail?.data !== null){
            if (typeof(props?.detail?.data?.locations?.provinces?.id) !== "undefined" && props?.detail?.data?.locations?.provinces?.id !== null){
                GetRegencies({
                    params: {
                        limit: 50,
                        page: 1,
                        province_id: props?.detail?.data?.locations?.provinces?.id
                    }
                })
            }
        }
     },[props?.detail,GetRegencies])
    useEffect(()=> {
        if(!props?.detail?.loading  && props?.detail?.data !== null){
            if (typeof(props?.detail?.data?.locations?.regencies?.id) !== "undefined" && props?.detail?.data?.locations?.regencies?.id !== null){
                GetDistricts({
                    params: {
                        limit: 20,
                        page: 1,
                        regency_id: props?.detail?.data?.locations?.regencies?.id
                    }
                })
            }
        }
     },[props?.detail,GetDistricts])
    useEffect(()=> {
        if(!props?.detail?.loading  && props?.detail?.data !== null){
            if (typeof(props?.detail?.data?.locations?.districts?.id) !== "undefined" && props?.detail?.data?.locations?.districts?.id !== null){
                GetVillages({
                    params: {
                        limit:20,
                        page:1,
                        district_id: props?.detail?.data?.locations?.districts?.id
                    }
                })
            }
        }
     },[props?.detail,GetDistricts])

    return (
        <Card title="Locations">

            <Form.Item name={['locations', "provinces"]} label={'Province'}
                       rules={[{required: true, message: "Please input your province"}]}>
                <Select placeholder={'Pilih Provinsi'} onChange={(value, options) => {
                    selectedProvince(value, options?.data)
                }} loading={province?.loading}>
                    {
                        province?.data?.map((item, index) => (
                            <Select.Option value={item?._id} key={item?._id} data={item}>
                                <Badge color={"purple"}/>
                                <span
                                    className="text-capitalize font-weight-semibold">{item?.name?.toString().toLowerCase()}</span>
                            </Select.Option>
                        ))
                    }
                </Select>
                {/*<Input placeholder={'please input Province'}/>*/}
            </Form.Item>

            <Form.Item name={['locations', "regencies"]} label={'City'}
                       rules={[{required: true, message: "Please input your province"}]}>
                <Select placeholder={'Pilih Kota/Kabupaten'} onChange={(value, options) => {
                    selectRegencies(value, options?.data)
                }} loading={regencies.loading}>
                    {
                        regencies?.data?.map((item, index) => (
                            <Select.Option value={item?._id} key={item?._id} data={item}>
                                <Badge color={"purple"}/>
                                <span
                                    className="text-capitalize font-weight-semibold">{item?.name?.toString().toLowerCase()}</span>
                            </Select.Option>
                        ))
                    }
                </Select>
            </Form.Item>
            <Form.Item name={['locations', "districts"]} label={'district'}
                       rules={[{required: true, message: "Please input your province"}]}>
                <Select placeholder={'Pilih Kota/Kabupaten'} onChange={(value, options) => {
                    selectDistrict(value,options?.data ?? {})
                }} loading={districts.loading}>
                    {
                        districts?.data?.map((item, index) => (
                            <Select.Option value={item?._id} key={item?._id} data={item}>
                                <Badge color={"purple"}/>
                                <span
                                    className="text-capitalize font-weight-semibold">{item?.name?.toString().toLowerCase()}</span>
                            </Select.Option>
                        ))
                    }
                </Select>
            </Form.Item>

            <Form.Item name={['locations', "sub_districts"]} label={'Sub District'}
                       rules={[{required: true, message: "Please input your sub district"}]}>
                <Select placeholder={'Pilih Kota/Kabupaten'} onChange={(value, options) => {
                    // selectDistrict(value,options?.data)
                }} loading={vilages.loading}>
                    {
                        vilages?.data?.map((item, index) => (
                            <Select.Option value={item?._id} key={item?._id} data={item}>
                                <Badge color={"purple"}/>
                                <span
                                    className="text-capitalize font-weight-semibold">{item?.name?.toString().toLowerCase()}</span>
                            </Select.Option>
                        ))
                    }
                </Select>
            </Form.Item>


            <Form.Item name={['locations', "address"]} label={'Detail'}
                       rules={[{required: true, message: "Please input your address detail"}]}>
                <Input.TextArea rows={4}/>
            </Form.Item>
            <Form.Item name={['locations', "coordinates",1]} label={'Latitude'}
                       rules={[{required: true, message: "Please input your Latitude"}]}>
                <Input/>
            </Form.Item>
            <Form.Item name={['locations', "coordinates",0]} label={'Longitude'}
                       rules={[{required: true, message: "Please input your Longitude"}]}>
                <Input/>
            </Form.Item>
            <Form.Item name={['locations', "zip_code"]} label={'ZIP Code'}
                       rules={[{required: true, message: "Please input your zip code"}]}>
                <InputNumber placeholder={'please input zipcode'}/>
            </Form.Item>


        </Card>
    )
}

LocationField.propTypes = {
    GetProvinces: PropTypes.func.isRequired,
    GetRegencies: PropTypes.func.isRequired,
    GetVillages: PropTypes.func.isRequired,
    GetDistricts: PropTypes.func.isRequired,
}
LocationField.defaultProps = {}

const mapStateToProps = ({Locations,Villa}) => {
    const {province} = Locations
    const { detail } = Villa
    const {regencies,districts,vilages} = Locations
    return {
        detail,
        province,
        districts,
        regencies,
        vilages
    }
}
const mapDispatchToProps = {
    GetProvinces,
    GetDistricts,
    GetVillages,
    GetRegencies
}
export default connect(mapStateToProps, mapDispatchToProps)(LocationField)
