import React, {useState} from 'react'
import PropTypes from 'prop-types'
import {connect} from 'react-redux'
import CustomIcon from 'components/util-components/CustomIcon'
import {LoadingOutlined} from '@ant-design/icons';
import Utils from "../../../../utils";
import {message, Upload, Card, notification} from "antd";
import {ImageSvg} from 'assets/svg/icon';
import ApiService from "../../../../services/Api.service";

const {Dragger} = Upload;

const ThumbnailField = (props) => {
    const [progressed, setProgressed] = useState(0)
    const [fileList,setFileList] = useState({})
    const [ files,setFiles] = useState([])
    const [imageUrl, setImageUrl] = useState(() => {
        if (props?.data !== null) {
            return [props?.data?.thumbnail?.prefix_url, props?.data?.thumbnail?.filename].join("/")
        }
        return ""
    })
    const [loading, setLoading] = useState(false)
    const beforeUpload = file => {
        const isJpgOrPng = file.type === 'image/jpeg' || file.type === 'image/png' || file.type === 'image/jpg';
        if (!isJpgOrPng) {
            message.error('You can only upload JPG/PNG file!');
        }
        const isLt2M = file.size / 1024 / 1024 < 2;
        if (!isLt2M) {
            message.error('Image must smaller than 2MB!');
        }
        return isJpgOrPng && isLt2M;
    }
    const onUploadChange = async (info) => {
        const {onSuccess, onError, file, onProgress} = info;
        setFiles([file])
        setFileList(file)

        const formdata = new FormData()
        formdata.append(`files`, file)

        await Utils.getBase64(file, imgUrl => {
            setImageUrl(imgUrl)
        });
        await new ApiService({
            url: `/api/v1/villa/self/update/thumbnail`,
            body: formdata,
            config: {
                headers: {
                    "Authorization": `Bearer ${Utils.getToken('get')}`,
                    "Content-Type": "application/formdata"
                },
                onUploadProgress: event => {
                    let progres = (event.loaded / event.total) * 100
                    setProgressed(progres)
                    onProgress({percent: (event.loaded / event.total) * 100}, file);
                    if(progres === 100){

                    }

                }
            }
        })
            .put()
            .then(({data}) => {
                onSuccess(file);

                notification.success({
                    message: 'Success save to database',
                    description: 'the data has been successfully updated',
                    duration: 2
                });
                // setLoading(false)
            })
            .catch((err) => {
                const error = new Error('Some error');
                onError({event: error});
                message.error({
                    content: err?.message ??  "Successfully!",
                    key: "updatable",
                    duration: 2
                })
            })
    }
    return (
        <>
            <Card bordered={false} title={'Thumbnail'}>
                <Dragger
                    accept="image/*"
                    multiple={false}
                    listType="card"
                    progress={{
                        strokeColor: {
                            '0%': '#108ee9',
                            '100%': '#87d068',
                        },
                        strokeWidth: 3,
                        format: percent => percent && `${parseFloat(percent.toFixed(2))}%`,
                    }}
                    maxCount={1}
                    onRemove={false}
                    file={fileList}
                    customRequest={onUploadChange}
                    className={'image-upload-grid'}
                    beforeUpload={beforeUpload}
                >
                    {
                        imageUrl ?
                            <img src={imageUrl} alt="avatar"
                                 className="absolute top-0 left-0 rounded-xl w-full h-full object-cover"/>
                            :
                            <div>
                                {
                                    loading ?
                                        <div>
                                            <LoadingOutlined className="font-size-xxl text-primary"/>
                                            <div className="mt-3">Uploading</div>
                                        </div>
                                        :
                                        <div>
                                            <CustomIcon className="display-3" svg={ImageSvg}/>
                                            <p>Click or drag file to upload</p>
                                        </div>
                                }
                            </div>
                    }
                </Dragger>
            </Card>
        </>
    )
}

ThumbnailField.propTypes = {}
ThumbnailField.defaultProps = {}
export default connect(({Villa}) => {
    const {detail} = Villa
    const {data, loading} = detail
    return {data, loading}
}, {})(ThumbnailField)