import React, {useState,useEffect} from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import {Card, Form, Input} from 'antd'

const SocialField = props => {
    let {card} = props
    const [formFields] = useState({
        instagram: {
            name: ['social','instagram'],
            label: "Instagram",
            placeholder:"https://www.instagram.com/your_username",
            rules:[
                {
                    required:false,
                    message: "instagram is Required!"
                }
            ]
        },
        facebook: {
            name: ['social','facebook'],
            label: "Facebook",
            placeholder:"https://www.facebook.com/your_username",
            rules:[
                {
                    required:false,
                    message: "Facebook is Required!"
                }
            ]
        },
        youtube: {
            name: ['social','youtube'],
            label: "Youtube",
            placeholder:"https://www.youtube.com",
            rules:[
                {
                    required:false,
                    message: "Youtube is Required!"
                }
            ]
        },
        twitter: {
            name: ['social','twitter'],
            label: "Twitter",
            placeholder:"https://www.twitter.com",
            rules:[]
        },
    })
    return (
        <Card {...card}>
            {
                Object.keys(formFields).map((key)=> {
                    return(
                        <Form.Item
                            {...formFields[key]}
                        >
                            <Input placeholder={formFields[key]?.placeholder ?? ""}/>
                        </Form.Item>
                    )
                })
            }
        </Card>
    )
}


SocialField.propTypes = {
    card:PropTypes.shape({
        title: PropTypes.string,
        bordered:PropTypes.bool,
        extra: PropTypes.node,
        className: PropTypes.string
    })
}
SocialField.defaultProps = {
    card:{
        title:"Social",
        bordered:false,
        extra:null,
        className:""
    }
}

export default connect(()=> {
    return {}
},{})(React.memo(SocialField))