import React from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import {Card, Form, Input,Row,Col} from "antd";

const ProfileField = (props)=> {
    let { withCard } = props
    return withCard ? (
        <Card bordered={false}>
            <Row gutter={24}>
                <Col xs={24} md={12}>
                    <Form.Item label={'Name'} name={'name'} rules={[{required:true,message:"is Required!"}]}>
                        <Input/>
                    </Form.Item>
                </Col>
                <Col xs={24} md={12}>
                    <Form.Item label={'Slug'} name={'slug'} rules={[{required:true,message:"is Required!"}]}>
                        <Input/>
                    </Form.Item>
                </Col>
                <Col xs={24} md={24}>
                    <Form.Item label={'Bio'} name={'bio'} rules={[{required:true,message:"is Required!"}]}>
                        <Input/>
                    </Form.Item>

                </Col>
                <Col xs={24} md={24}>
                    <Form.Item label={'Description'} name={'description'}>
                        <Input.TextArea allowClear autoSize={{
                            maxRows:6,minRows:4
                        }}/>
                    </Form.Item>
                </Col>

            </Row>
        </Card>
    ):
        <Row gutter={24}>
        <Col xs={24} md={12}>
            <Form.Item label={'Name'} name={'name'} rules={[{required:true,message:"is Required!"}]}>
                <Input/>
            </Form.Item>
        </Col>
        <Col xs={24} md={12}>
            <Form.Item label={'Slug'} name={'slug'} rules={[{required:true,message:"is Required!"}]}>
                <Input/>
            </Form.Item>
        </Col>
        <Col xs={24} md={24}>
            <Form.Item label={'Bio'} name={'bio'} rules={[{required:true,message:"is Required!"}]}>
                <Input/>
            </Form.Item>

        </Col>
        <Col xs={24} md={24}>
            <Form.Item label={'Description'} name={'description'}>
                <Input.TextArea allowClear autoSize={{
                    maxRows:6,minRows:4
                }}/>
            </Form.Item>
        </Col>

    </Row>
}

ProfileField.propTypes = {
    withCard: PropTypes.bool.isRequired
}
ProfileField.defaultProps = {
    withCard:true
}

export default connect(()=> {
    return {}
},{})(React.memo(ProfileField))