import React, {useEffect, useState} from 'react'
import PropTypes from 'prop-types'
import {connect} from 'react-redux'
import {PageHeaderAlt} from "../../../../components/layout-components/PageHeaderAlt";
import {Avatar, Button, Card, Col, Row, Form, Tabs, message, Typography, Tooltip, notification} from "antd";
import Flex from "../../../../components/shared-components/Flex";
import {MailOutlined, PhoneOutlined, HomeOutlined, FileAddOutlined, GlobalOutlined} from '@ant-design/icons'
import Icon from "../../../../components/util-components/Icon";

import LocationField from "./LocationField";
import SocialField from "./SocialField";
import {useLocation} from "react-router-dom";
import ProfileField from "./ProfileField";
import PhotoFields from "./PhotoFields";
import VideosField from "./VideosField";
import Utils from "../../../../utils";
import ContactField from "./ContactField";
import FacilityField from "./FacilityField";
import ApiService from "../../../../services/Api.service";
import {_getMyVillaProfile} from "../../../../redux/actions";
import Loading from "../../../../components/shared-components/Loading";
import ThumbnailField from "./ThumbnailField";

const VillaProfile = (props) => {
    let {detail,_getMyVillaProfile} = props

    const [form] = Form.useForm()
    const locations = useLocation()

    const [tabs, setTab] = useState(() => {
        let tab = new URLSearchParams(locations.search).get("tab")
        if (tab) {
            return tab.toString()?.toLowerCase()
        }
        return "general"
    })

    useEffect(()=> {
        _getMyVillaProfile({})
    },[_getMyVillaProfile])


    function setUp(value) {
        if (window) {
            let prefix = "?tab="
            let newUrl = [window.origin, locations.pathname].join("")
            window.history.pushState({article: 'read-all'}, 'Read All Article', [newUrl, prefix, value].join(""));
        }
    }

    function onTabs(value) {
        setUp(value)
    }

    const [photos, setPhotos] = useState([])
    const [uploadedImg, setImage] = useState('')
    const [uploadLoading, setUploadLoading] = useState(false)
    const [submitLoading, setSubmitLoading] = useState(false)
    const handleUploadChange = info => {
        let {fileList} = info
        let key = 'updatable'
        message.loading({content: "loading...", key, duration: 1})
        if (props?.mode === "UPDATE") {
            console.log({mode: props?.mode, fileList, info})
            if (typeof (fileList) !== "undefined" && Array.isArray(fileList)) {
                for (let i = 0; i < fileList.length; i++) {
                    Reflect.set(fileList[i], "status", "done")

                    message.success({content: "Successfully!", key, duration: 2})
                    Utils.getBase64(fileList[i].originFileObj, imageUrl => {
                        setImage(imageUrl)
                        setUploadLoading(true)
                    });
                }
            }
            setPhotos(fileList)
        } else {
            if (typeof (fileList) !== "undefined" && Array.isArray(fileList)) {
                for (let i = 0; i < fileList.length; i++) {
                    Reflect.set(fileList[i], "status", "done")

                    message.success({content: "Successfully!", key, duration: 2})
                    Utils.getBase64(fileList[i].originFileObj, imageUrl => {
                        setImage(imageUrl)
                        setUploadLoading(true)
                    });
                }
            }
            setPhotos(fileList)
        }

    };


    const onFinish = () => {
        message.loading({content: "Loading...", key: "updatable",duration:10})
        form.validateFields()
            .then(async (value) => {
                console.log({value})
                await new ApiService({
                    url: `/api/v1/villa/self/profile`,
                    body: value
                }).patch()
                    .then(async (result) => {

                        if(!result?.error){
                            if(photos.length > 0){
                                message.loading({content: "Photo uploaded", key: "updatable",duration:10})
                                const formdata = new FormData()
                                for (let i = 0; i < photos.length; i++) {
                                    formdata.append(`files`, photos[i].originFileObj)

                                }
                                await new ApiService({
                                    url: `/api/v1/villa/self/profile/photo`,
                                    body: formdata,
                                    config: {
                                        headers: {
                                            "Authorization": `Bearer ${Utils.getToken('get')}`,
                                            "Content-Type": "application/formdata"
                                        },
                                        onUploadProgress: event => {
                                            let eventProgress = (event.loaded / event.total) * 100
                                            console.log({eventProgress,event})
                                        }
                                    }
                                })
                                    .put()
                                    .then(({data}) => {
                                        message.success({
                                            content: data?.message ?? result?.message ?? "Successfully!",
                                            key:"updatable",
                                            duration: 2
                                        })
                                        notification.success({
                                            message: 'Success save to database',
                                            description: 'the data has been successfully updated',
                                            duration: 2
                                        });
                                    })
                                    .catch((err) => {
                                        message.error({
                                            content: err?.message ?? result?.message ?? "Successfully!",
                                            key:"updatable",
                                            duration: 2
                                        })
                                    })
                            }

                        }else{
                            message.error({content: result?.message ?? "Some Error", key: "updatable", duration: 3})
                        }
                    })
                    .catch((err)=> {
                        notification.error({
                            message: 'Error save to database',
                            description: 'the data has been Error',
                            duration: 5
                        });
                    })

            })
            .catch((err) => {
                console.log({err})
                setTimeout(() => {
                    message.error({content: "fill in all required input forms", key: "updatable", duration: 3})
                }, 1000)
            })
    }

    const ExtraComponent = () => {
        return (
            <div>
                <Tooltip title={'Save Profile'}>
                    <Button
                        size={'small'}
                        type={'primary'}
                        ghost
                        icon={<FileAddOutlined/>}
                        onClick={onFinish}
                    >Save</Button>
                </Tooltip>
            </div>
        )
    }

    useEffect(() => {
        if (!detail?.loading && detail?.data !== null) {
            let fields = {}
            if (detail?.data?.locations) {
                let locations = {}
                Reflect.set(locations, 'provinces', detail?.data?.locations?.provinces?._id)
                Reflect.set(locations, 'regencies', detail?.data?.locations?.regencies?._id)
                Reflect.set(locations, 'districts', detail?.data?.locations?.districts?._id)
                Reflect.set(locations, 'sub_districts', detail?.data?.locations?.sub_districts?._id)
                Reflect.set(locations, 'address', detail?.data?.locations?.address)
                Reflect.set(locations, 'zip_code', detail?.data?.locations?.zip_code)
                Reflect.set(locations, 'coordinates', detail?.data?.locations?.coordinates)

                Reflect.set(fields, 'locations', locations)
                Reflect.deleteProperty(detail?.data, 'locations')
            }
            fields = {
                ...fields,
                ...detail?.data,
            }


            form.setFieldsValue({
                ...fields,
            })
        }

    }, [form.setFieldsValue, detail])

    return detail?.data === null ? <Loading cover={'content'}/> : (
        <>
            <PageHeaderAlt background="/img/others/img-12.jpg" cssClass="bg-primary" overlap>
                <div className="container text-center">
                    <div className="my-md-5">
                    </div>
                </div>
            </PageHeaderAlt>
            <div className="container my-4">

                <Card bordered={false} title={'Villa Profile Update'} extra={<ExtraComponent/>}
                      loading={detail?.loading ?? false}>
                    <Form
                        initialValues={{}}
                        form={form}
                        layout={'vertical'}
                    >
                        <Row gutter={24}>
                            <Col xs={{
                                span: 24,
                                order: 2,
                            }} md={{
                                span: 6,
                                order: 1
                            }}>
                                <LocationField form={form}/>
                            </Col>
                            <Col xs={{
                                span: 24,
                                order: 1,
                            }} md={{
                                span: 18,
                                order: 2
                            }}>
                                <Tabs defaultActiveKey={tabs} onChange={onTabs}>
                                    <Tabs.TabPane tab="General" key="general">
                                        <Row gutter={24}>
                                            <Col xs={24} md={8}>
                                                <ThumbnailField/>
                                            </Col>
                                            <Col xs={24} md={16}>
                                                <ProfileField {...props}/>
                                            </Col>
                                            <Col md={24}>
                                                <ContactField {...props}/>
                                                <SocialField {...props} card={{
                                                    title: "Socials",
                                                    bordered: true,
                                                    className: "p-0 m-0",
                                                    style: {
                                                        padding: 0,
                                                        margin: 0
                                                    }
                                                }}/>
                                            </Col>
                                        </Row>
                                    </Tabs.TabPane>
                                    <Tabs.TabPane tab="Facility" key="facility">
                                        <FacilityField {...props}/>
                                    </Tabs.TabPane>
                                    <Tabs.TabPane tab="Photos" key="photos">
                                        <PhotoFields
                                            mode={'UPDATE'}
                                            photos={photos}
                                            setPhotos={setPhotos}
                                            uploadedImg={uploadedImg}
                                            uploadLoading={uploadLoading}
                                            handleUploadChange={handleUploadChange}
                                        />
                                    </Tabs.TabPane>
                                    <Tabs.TabPane tab="Videos" key="videos" disabled>
                                        <VideosField {...props}/>
                                    </Tabs.TabPane>
                                </Tabs>

                            </Col>

                        </Row>
                    </Form>
                </Card>

            </div>
        </>
    )
}

VillaProfile.propTypes = {
    _getMyVillaProfile: PropTypes.func.isRequired
}
VillaProfile.defaultProps = {}

export default connect(({Villa}) => {
    let {detail} = Villa
    return {detail}

}, {_getMyVillaProfile})(React.memo(VillaProfile))