import React from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import {Card, Form, Input} from 'antd'
const ContactField = (props)=> {
    return (
        <Card bordered={true} title={'Contact'}>
            <Form.Item
                label={'Email'}
                rules={[
                    {
                        required:true,
                        message:"Email must be filled in"
                    }
                ]}
                name={['contact','email']}>
                <Input/>
            </Form.Item>
            <Form.Item
                rules={[
                    {
                        required:true,
                        message:"Whatsapp must be filled in"
                    }
                ]}
                label={'Whatsapp'}
                name={['contact','whatsapp']}>
                <Input/>
            </Form.Item>
            <Form.Item
                rules={[
                    {
                        required:true,
                        message:"Phone Number be filled in"
                    }
                ]}
                label={'Phone Number'}
                name={['contact','phone_number']}>
                <Input/>
            </Form.Item>
        </Card>
    )
}
ContactField.propTypes = {}
ContactField.defaultProps = {}

export default connect(()=> {
    return {}
},{})(React.memo(ContactField))